html,
body{
	height:100%;
	position: relative;
	color: @color-primary;
	font-family: @standard;
	font-weight: @light;
	font-size: @base-font-size;
	line-height: @base-line-height;	
}

body{
	width: 100%;
	overflow-x: hidden;
}

::selection {
	background-color: rgba(255, 255, 255, 1);
	.directions_color;
}
::-moz-selection {
	background-color: rgba(255, 255, 255, 1);
	.directions_color;
}

#master{
	position: absolute;
	top: 0;
	left: 0;
	overflow-x: hidden;
	overflow-y: scroll;
	height: 100%;
	width: 100%;
	-webkit-overflow-scrolling: touch;
}

#main{	
	width: 100%;
	min-height: 100%;

	.iPad [data-entering="list"] &{
		overflow: scroll;
	    -webkit-overflow-scrolling: touch;
	}
}

.home{
	padding: 0 0 50px !important;
}

.colophon{
	padding-bottom: 50px !important;
}

.interior{
	max-width: @interior_width;
	padding: 0 20px;
	margin: 0 auto;

	.mobileMenuOpen &{
		position: relative;
		z-index: 0;
	}
}

.footer{
	position: relative;
	float: left;
	width: 100%;
	left: 0;
	perspective: 1000px;
	height: @height_footer;
	background-color: @color-copy;
	color: @color-copy-contrast;

	.transform(translate3d(0, 0, 0));
	.transition(all .5s @eio);
}
